import React, { useState } from 'react';
import calculate from '../logic/calculate';

function Calculator() {
  const [total, setTotal] = useState(0);
  const [next, setNext] = useState(null);
  const [operation, setOperation] = useState(null);

  const handleClick = (value) => {
    const result = calculate({ total, next, operation }, value);
    setTotal(result.total);
    setNext(result.next);
    setOperation(result.operation);
  };

  return (
    <div id="main-container">
      <div id="calc-screen">
        <div className="calcscr previous-op" />
        <div className=" calcscr current-op">
          <p>
            { total }
            { operation }
            { next }
          </p>
        </div>
      </div>
      <div id="calc-buttons">
        <button type="button" className="AC grey-btn" id="AC" handleClick={handleClick}>AC</button>
        <button type="button" className="PlusMinus grey-btn" id="+/-" handleClick={handleClick}>+/-</button>
        <button type="button" className="Percent grey-btn" id="%" handleClick={handleClick}>%</button>
        <button type="button" className="Div ora-btn" id="รท" handleClick={handleClick}>รท</button>
        <button type="button" className="Num-btn grey-btn" id="7" handleClick={handleClick}>7</button>
        <button type="button" className="Num-btn grey-btn" id="8" handleClick={handleClick}>8</button>
        <button type="button" className="Num-btn grey-btn" id="9" handleClick={handleClick}>9</button>
        <button type="button" className="Times ora-btn" id="x" handleClick={handleClick}>x</button>
        <button type="button" className="Num-btn grey-btn" id="4" handleClick={handleClick}>4</button>
        <button type="button" className="Num-btn grey-btn" id="5" handleClick={handleClick}>5</button>
        <button type="button" className="Num-btn grey-btn" id="6" handleClick={handleClick}>6</button>
        <button type="button" className="Minus ora-btn" id="-" handleClick={handleClick}>-</button>
        <button type="button" className="Num-btn grey-btn" id="1" handleClick={handleClick}>1</button>
        <button type="button" className="Num-btn grey-btn" id="2" handleClick={handleClick}>2</button>
        <button type="button" className="Num-btn grey-btn" id="3" handleClick={handleClick}>3</button>
        <button type="button" className="Plus ora-btn" id="+" handleClick={handleClick}>+</button>
        <button type="button" className="Zero span-2" id="0" handleClick={handleClick}>0</button>
        <button type="button" className="Dot grey-btn" id="." handleClick={handleClick}>.</button>
        <button type="button" className="Equal ora-btn" id="=" handleClick={handleClick}>=</button>
      </div>
    </div>
  );
}

export default Calculator;
